      h5.mb-0 Concepto
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-9.order-lg-1.order-2.mb-lg-0
        p(data-aos="fade-left") El transporte se define como el traslado de personas, animales o cosas de un punto a otro, a travs de un medio fsico. Para efectos de la normativa, la movilizacin de animales en pie abarca todas las actividades que se ejecutan para mover a los animales, bien sea por s mismos o por cualquier otro medio utilizado para trasladarlos. 
        p(data-aos="fade-left") Un vehculo de transporte de animales en pie, es el medio de desplazamiento o de transporte de animales de un punto a otro, incluyendo embarcaciones aparatos montados sobre ruedas. Por lo general, se utiliza un camin (automotor) para transportar la carga, dado su tamao y destino.
        .row.justify-content-center.align-items-center
          .col-lg-1.p-0.px-2.d-none.d-lg-block
            figure(data-aos="zoom-in")
              img.img-fluid(src="@/assets/curso/temas/intro/img4.svg" alt="Icono 1")
          .col-lg-11
            p.mb-0 Este tipo de vehculo, es aquel diseado o adaptado especficamente para movilizar animales vivos (bovinos, porcinos, ovinos, caprinos, equinos, aves, entre otros) por carretera, garantizando condiciones adecuadas de bienestar animal, bioseguridad, sanidad y seguridad vial durante todo el proceso de cargue, transporte y descargue.
      .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img2.png" alt="Imagen decorativo")
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de vehculos
    p.mb-4(data-aos="zoom-in") Los vehculos pueden clasificarse segn el tipo de especie a transportar y su estructura: 
    .row.justify-content-center.mb-4
        .col-lg-5.mb-4.mb-lg-0
          .tarjeta-avatar(style="padding-top: 0px !important;").h-100          
              .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
                img(src='@/assets/curso/temas/tema1/img3.png', alt='Imagen decorativa')
                .p-4
                  h5 Camin
                  p(data-aos="fade-left") Vehculo automotor que por su tamao y destino se usa para transportar carga. Se encuentran los siguientes tipos:
                  ul.lista-ul
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Camiones de un solo piso:] usados para bovinos, equinos y bufalinos.
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Camiones de dos o ms pisos:] adecuados para especies pequeas como aves, cerdos o cabras.
        .col-lg-5
          .tarjeta-avatar(style="padding-top: 0px !important;").h-100            
              .tarjeta.color-primarios.w-100(style="padding-top: 0px !important;")
                img(src='@/assets/curso/temas/tema1/img4.png', alt='Imagen decorativa')
                .p-4
                  h5 Remolque y semirremolque
                  p(data-aos="fade-left") Vehculos no motorizados halados por una unidad tractora. Existen de tipo:
                  ul.lista-ul
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Remolques o trileres especializados:] con compartimentos o guacales para aves o animales pequeos.
                    li
                      i.fas.fa-horse-head
                      p.mb-0 #[b Vehculos con guacales o jaulas desmontables:] especialmente para aves de corral o animales menores.
    p.mb-4 Complementando lo anterior, se invita a que analice las siguientes imgenes:
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-left")
          h5 Figura 1.
          i  Tipos de vehculos
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10.movil(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema1/img5_1.png', alt='Descripcin: La imagen muestra una escena ilustrada en un entorno al aire libre con un fondo verde plano. En el centro de la imagen, hay una plataforma beige que parece ser parte de una infraestructura "sin barreras" diseada para la accesibilidad. A la izquierda, se observa a una mujer con cabello castao que lleva gafas oscuras y tiene un bastn blanco, lo que indica que tiene discapacidad visual; viste una camiseta amarilla, falda roja y mochila roja. Ella est caminando sobre una lnea tctil de color amarillo brilloso que se encuentra en la superficie, diseada para guiar a las personas con problemas de visin. A la derecha, hacia el fondo, hay un hombre sentado en una silla de ruedas, vestido con una camiseta naranja y pantalones cortos oscuros, mientras sube por una rampa accesible que tiene bordes elevados blancos para seguridad. La rampa est conectada con la plataforma tctil, demostrando un diseo universal que facilita el acceso tanto para personas con discapacidad visual como para aquellas con movilidad reducida. En la parte inferior izquierda de la imagen aparece el texto "BARRIER FREE", resaltando el propsito de promover la inclusin y la accesibilidad. La ilustracin transmite un mensaje positivo, mostrando un entorno funcional y pensado para garantizar la igualdad de oportunidades y la comodidad de las personas con discapacidades. Este diseo simple pero efectivo refleja un ambiente inclusivo y optimista.')
      .col-lg-10.desktop(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema1/img5.png', alt='Descripcin: La imagen muestra una escena ilustrada en un entorno al aire libre con un fondo verde plano. En el centro de la imagen, hay una plataforma beige que parece ser parte de una infraestructura "sin barreras" diseada para la accesibilidad. A la izquierda, se observa a una mujer con cabello castao que lleva gafas oscuras y tiene un bastn blanco, lo que indica que tiene discapacidad visual; viste una camiseta amarilla, falda roja y mochila roja. Ella est caminando sobre una lnea tctil de color amarillo brilloso que se encuentra en la superficie, diseada para guiar a las personas con problemas de visin. A la derecha, hacia el fondo, hay un hombre sentado en una silla de ruedas, vestido con una camiseta naranja y pantalones cortos oscuros, mientras sube por una rampa accesible que tiene bordes elevados blancos para seguridad. La rampa est conectada con la plataforma tctil, demostrando un diseo universal que facilita el acceso tanto para personas con discapacidad visual como para aquellas con movilidad reducida. En la parte inferior izquierda de la imagen aparece el texto "BARRIER FREE", resaltando el propsito de promover la inclusin y la accesibilidad. La ilustracin transmite un mensaje positivo, mostrando un entorno funcional y pensado para garantizar la igualdad de oportunidades y la comodidad de las personas con discapacidades. Este diseo simple pero efectivo refleja un ambiente inclusivo y optimista.')

      
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Requisitos tcnicos
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-3.col-sm-6.col-8.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img6.png" alt="Imagen decorativo")
      .col-lg-9.mb-lg-0
        p(data-aos="fade-left") Los requisitos tcnicos segn el Manual ICA-Mintransporte, se definen como el conjunto estandarizado de condiciones de habitabilidad mvil y protocolos de trato humanitario que buscan transformar el transporte de animales: pasando de ser un simple traslado de mercanca a un proceso de custodia temporal de seres vivos, asegurando que lleguen a su destino con la mnima de peso, sin lesiones y aptos para el consumo humano (si es el fin).        
        p(data-aos="fade-left") Se trata de un ecosistema de #[b condiciones fsicas, sanitarias y operativas] que deben garantizarse obligatoriamente para mover animales en pie por el territorio nacional. El objetivo central ya no es solo el transporte de carga, sino la preservacin del bienestar animal y la inocuidad alimentaria.
        p.mb-0 Se pueden estructurar los requisitos tcnicos en tres grandes dimensiones segn dicho manual:
      
    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 A.	Requisitos de la infraestructura del vehculo
    p.mb-4(data-aos="zoom-in") Son las adaptaciones fsicas que debe tener el camin, barco o vehculo para que el animal no sufra daos ni estrs trmico. No basta con que el camin ruede; debe ser un entorno seguro y tener las siguientes condiciones: 
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10.tarjeta--BG12.tarjeta.p-4(data-aos="zoom-in")
        .row.justify-content-center.align-items-center
          .col-lg-10.movil
            figure
              img.img-fluid(src="@/assets/curso/temas/tema1/img7_1.svg" alt="Imagen decorativo")
          .col-lg-10.desktop(data-aos="zoom-in")
            ImagenInfografica.color-acento-botones
              template(v-slot:imagen)
                figure
                  img(src='@/assets/curso/temas/tema1/img7.svg', alt='Texto que describa la imagen')
              .tarjeta.color-secundario.p-3(x="25%" y="22%" numero="+")            
                h5 Proteccin climtica
                p.mb-0 El vehculo debe tener carpa o techo que proteja del sol directo y la lluvia, pero que permita la circulacin de aire.
              .tarjeta.color-secundario.p-3(x="25%" y="40.5%" numero="+")
                h5 Ventilacin y espacio
                p.mb-0 Este es un punto crtico. El manual exige una altura mnima especfica (por ejemplo, para porcinos) y una densidad de carga controlada (metros cuadrados por animal) para garantizar un flujo de aire suficiente.
              .tarjeta.color-secundario.p-3(x="27%" y="65.3%" numero="+")
                h5 Pisos y superficies
                p.mb-0 Deben ser antideslizantes y estar hechos de materiales lavables y desinfectables. No puede haber aristas, clavos salientes ni huecos donde un animal pueda fracturarse.
              .tarjeta.color-secundario.p-3(x="50%" y="47%" numero="+")
                h5 Separaciones fsicas
                p.mb-0 Si se transportan diferentes especies o grupos incompatibles, el vehculo debe tener barreras fsicas internas.
    
    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 B.	Requisitos operativos y de manejo
    p.mb-4 Se refiere a cmo se ejecuta la labor. El manual transforma al transportador en una garanta del bienestar, prohibiendo prcticas tradicionales que hoy se consideran maltrato. Debe tener en cuenta lo siguiente:
    .row.mb-5
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img8.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center Cargue y descargue
              p.mb-0 Se exige el uso de rampas con una inclinacin mxima (generalmente no mayor a 20) para evitar infartos o cadas. Est prohibido el uso de picanas elctricas (salvo excepciones muy especficas), palos con puntas o gritos excesivos.
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img9.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center Planificacin de ruta
              p.mb-0 El transportador debe planear paradas para descanso, hidratacin y alimentacin si el viaje supera ciertas horas.
      .col-lg-4
        .tarjeta-avatar
          img(src='@/assets/curso/temas/tema1/img10.svg' alt='AvatarTop')
          .tarjeta.color-primario.tarjeta.color-primarios2.w-100
            .p-4
              h5.text-center Capacitacin
              p.mb-0 El conductor y los operarios deben tener conocimientos bsicos certificables sobre comportamiento y manejo animal (sin estrs).
    .titulo2.mb-4(data-aos="fade-left")
      h5.mb-0 C.	Requisitos documentales y de trazabilidad
    p.mb-4(data-aos="zoom-in") Es la evidencia formal de que los dos puntos anteriores y la sanidad del animal estn en orden. Por lo cual se debe realizar lo siguiente:
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-3.col-sm-6.col-8.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img(src='@/assets/curso/temas/tema1/img14.png', alt='Imagen decorativa')
      .col-lg-9.mb-lg-0
        .row
            .col-lg-12
              .tarjeta-avatar-b.mb-4
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/temas/tema1/img11.svg' alt='AvatarTop').d-none.d-lg-block
                .tarjeta.tarjeta--verde
                  .p-4
                    h5 GSMI (Gua Sanitaria de Movilizacin Interna)
                    p.mb-0 Documento "rey" expedido por el ICA que certifica que los animales no tienen enfermedades restrictivas y vienen de un predio registrado.
            .col-lg-12
              .tarjeta-avatar-b.mb-4
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/temas/tema1/img12.svg' alt='AvatarTop').d-none.d-lg-block
                .tarjeta.tarjeta--verde
                  .p-4
                    h5 Gua de transporte
                    p.mb-0 Documento que legaliza el contrato de transporte.
            .col-lg-12
              .tarjeta-avatar-b.mb-0
                .tarjeta-avatar-b__img
                  img(src='@/assets/curso/temas/tema1/img13.svg' alt='AvatarTop').d-none.d-lg-block
                .tarjeta.tarjeta--verde
                  .p-4
                    h5 Registro del vehculo
                    p.mb-0 El vehculo debe estar registrado y habilitado para el transporte de animales vivos.
      
    p.mb-4(data-aos="zoom-in") Adems de tener en cuenta que segn el Manual de Procedimientos ICA-Min Transporte, los vehculos deben cumplir con los siguientes requisitos:
    AcordionA(tipo="a" clase-tarjeta="tarjeta tarjeta--azul")
      .row(titulo="Tcnicos")
        .row.justify-content-center.align-items-center
          .col-lg-8.order-lg-1.order-2.mb-lg-0
            ul.lista-ul
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Pisos:] resistentes, antideslizantes, impermeables y fciles de limpiar. Deben evitar lesiones y permitir el drenaje de lquidos sin contaminar la va pblica.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Paredes laterales:] lisas, sin salientes, con altura adecuada segn la especie (mnimo 120 cm para bovinos y 100 cm para porcinos y ovinos).
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Techos o cubiertas:] deben proteger contra el sol, la lluvia y el viento, permitiendo ventilacin adecuada.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Separadores internos:] para evitar aglomeraciones y peleas entre animales.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Puertas:] amplias (mnimo 90 cm de ancho), de fcil apertura y cierre, sin bordes cortantes.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Guacales o jaulas:] en caso de transporte de aves o animales pequeos, deben ser de material lavable, ventilado y con espacio suficiente.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Sistema de ventilacin:] natural o forzado, que evite acumulacin de gases y calor excesivo.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Equipo de monitoreo (opcional):] para registrar temperatura, humedad y condiciones del viaje.
          .col-lg-4.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
            figure
              img.img-fluid(src="@/assets/curso/temas/tema1/img15.png" alt="Imagen decorativo")
      .row(titulo="Mecnicos")
        .row.justify-content-center.align-items-center
          .col-lg-8.order-lg-1.order-2.mb-lg-0
            ul.lista-ul
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Sistema de suspensin:]en buen estado para evitar vibraciones excesivas.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Frenos y direccin:]deben estar en ptimas condiciones para garantizar seguridad vial.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Iluminacin interna y externa:] adecuada para operaciones nocturnas.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Sistema de limpieza:]debe permitir el lavado y desinfeccin del vehculo antes y despus del transporte.
              li
                i.fas.fa-minus(style="font-size: 8px;top:8px")
                p.mb-0 #[b Capacidad de carga:]no debe exceder el peso permitido ni el nmero mximo de animales por especie, segn el anexo tcnico del manual ICA.
              
          .col-lg-4.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
            figure
              img.img-fluid(src="@/assets/curso/temas/tema1/img16.png" alt="Imagen decorativo")

    separador
    #t_1_1.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.1 Tipos de diseo de carrocera
    
    .row.justify-content-center.mb-4
      .col-lg-3.col-6.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img17.png" alt="Imagen decorativo")
      .col-lg-9
        p(data-aos="fade-left") La carrocera del vehculo de transporte terrestre debe ser considerada como una #[b unidad de contencin temporal] diseada y construida para asegurar la proteccin de los animales contra lesiones, inclemencias climticas y estrs, tal como lo exige el principio de bienestar animal.
        p(data-aos="fade-left") Los tipos de diseo de carrocera para el transporte de animales en pie en Colombia estn regulados por la normativa del ICA y el Ministerio de Transporte y se clasifican segn la especie a transportar. 
        .titulo.mb-4(data-aos="fade-left")
          img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
          h5.mb-0 Tipos de vehculos
        p(data-aos="fade-left") Dentro de estas carroceras se pueden destacar los siguientes tipos de vehculos:
        .row.justify-content-center
          .col-lg-4.mb-4.mb-lg-0
            .tarjeta.color-primario.tarjeta.color-primarios2.tarjeta--BG01.p-4.h-100
              .row.justify-content-center.mb-3
                .col-lg-4.col-6.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/temas/tema1/img18.svg', alt='Texto que describa la imagen')
              .row.justify-content-center.mb-3
                .col-lg-8.fondoh3.p-2
                  h5.mb-0.text-center  Camin rgido
              p.text-center.mb-0 Vehculo automotor que por su tamao y destino se usa para transportar carga.
          .col-lg-4.mb-4.mb-lg-0
            .tarjeta.color-primario.tarjeta.color-primarios2.tarjeta--BG01.p-4.h-100
              .row.justify-content-center.mb-3
                .col-lg-4.col-6.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/temas/tema1/img19.svg', alt='Texto que describa la imagen')
              .row.justify-content-center.mb-3
                .col-lg-8.fondoh3.p-2
                  h5.mb-0.text-center Remolque
              p.text-center.mb-0 Vehculo no motorizado, halado por una unidad tractora a la cual no le transmite peso.
          .col-lg-4.mb-4.mb-lg-0
            .tarjeta.color-primario.tarjeta.color-primarios2.tarjeta--BG01.p-4.h-100
              .row.justify-content-center.mb-3
                .col-lg-4.col-6.mb-4.mb-lg-0
                  figure
                    img(src='@/assets/curso/temas/tema1/img19.svg', alt='Texto que describa la imagen')
              .row.justify-content-center.mb-3
                .col-lg-8.fondoh3.p-2
                  h5.mb-0.text-center Semirremolque
              p.text-center.mb-0 Vehculo sin motor, halado por un automotor sobre el cual se apoya y le transmite parte de su peso.
    p.mb-0 Incluyen carroceras tipo estaca, cerradas con ventilacin, con guacales modulares y remolques especializados, todas con requisitos tcnicos que garantizan el bienestar animal, la bioseguridad y la seguridad vial.

    separador
    #t_1_2.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.2 Tipos de corrales, equipo de monitoreo, especificaciones tcnicas (sistemas de vigilancia y microclima de carroceras) y uso
    p.mb-4(data-aos="zoom-in") Este punto se enfoca en los elementos internos del vehculo diseados para gestionar la carga, supervisar a los animales y controlar las condiciones ambientales durante el viaje.
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Tipos de corrales
    p.mb-4(data-aos="zoom-in") Los corrales o compartimentos tienen una funcin esencial en la segregacin y estabilidad de la carga, reduciendo el estrs y las lesiones por interaccin social. La siguiente tabla detalla los aspectos ms destacados de los corrales:
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 1.
      i Caractersticas de los corrales
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr
                th Aspecto
                th Detalle de la estructura y uso
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto")
                  b Objetivo de las divisiones
                td(data-label="Detalle de la estructura y uso")
                  | La unidad de transporte debe contar con 
                  b divisiones o compartimentos
                  |  (corrales) para organizar a los animales. Esto permite separarlos por 
                  b grupos de edad
                  | , 
                  b peso o sexo
                  |  segn las condiciones de bienestar animal.
                td(data-label="Documento y cita exacta")
                  b Manual, Numeral 2.2.c, literal i:
                  |  "Contar con 
