    p.mb-4(data-aos="zoom-in") El vehculo no es solo un medio de transporte, sino el entorno principal del animal durante la movilizacin. Debe estar diseado para protegerlos de las inclemencias climticas y prevenir lesiones. De igual manera, se deben tener en cuenta los siguientes aspectos tcnicos, normativos y de bienestar en cada parte del vehculo:
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-7(data-aos="fade-left")
        LineaTiempoD.color-acento-botones
          .row.justify-content-center(numero="1" titulo="Carrocera")
            .col-lg-12.order-lg-2.order-3
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnico:] debe estar construido con materiales resistentes, preferiblemente metlicos (aluminio) o plsticos de alta densidad, sobre madera. Se prohben bordes cortantes, puntillas o tornillos salientes que puedan lacerar a los animales. El diseo (tipo jaula, lona, furgn) debe adaptarse a la especie.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Normativo (Res. 20223040006915):] exige que los materiales sean de fcil limpieza y desinfeccin (bioseguridad) y que no sean txicos para los animales.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Bienestar:] un cuerpo liso y sin puntas previene el pnico y las heridas, que son una fuente directa de dolor y prdida econmica.
          .row.justify-content-center(numero="2" titulo="Pisos")
            .col-lg-12.order-lg-2.order-3
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnico:] es un punto crtico. Deben ser antideslizantes, utilizando estras, relieves (tipo "pata de mula"), recubrimientos de caucho o rejillas que permitan el drenaje de orina y heces, pero que no lastimen las pezuas o patas.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Normativo (Res. 20223040006915):] obliga a que los pisos eviten resbalones y cadas.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Bienestar:] previene la principal causa de lesiones graves: cadas que resultan en fracturas, contusiones severas y aplastamiento por "efecto domin" del resto de los animales.            
          .row.justify-content-center(numero="3" titulo="Ventilacin")
            .col-lg-12.order-lg-2.order-3
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnico:] debe ser adecuado a la especie. Puede ser pasivo (rejillas de ventilacin, aberturas) o forzado (extractores/ventiladores). Es crucial en especies muy susceptibles al estrs trmico, como los cerdos y las aves.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Normativo (Res. 20223040006915):] exige que el vehculo garantice la termorregulacin y la calidad del aire, evitando la acumulacin de gases (amoniaco) y el exceso de calor.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Bienestar:] una mala ventilacin provoca estrs trmico (hipertermia) o, en climas fros, hipotermia. El estrs trmico es una de las principales causas de muerte en el transporte de cerdos y aves.
          .row.justify-content-center(numero="4" titulo="Sistemas de separacin (Corrales)")
            .col-lg-12.order-lg-2.order-3
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnico:] son las divisiones internas del vehculo (puertas de guillotina, separadores mviles). Permiten gestionar la densidad de carga y separar lotes.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Normativo (Res. 20223040006915):] son obligatorias para cumplir con la separacin de animales de diferentes especies, tamaos o temperamentos (ej. con cuernos vs. sin cuernos).
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Bienestar:] evitan el aplastamiento de animales ms pequeos y las peleas entre grupos que no se conocen, reduciendo el estrs social y las lesiones.
          .row.justify-content-center(numero="5" titulo="Rampas de embarque")
            .col-lg-12.order-lg-2.order-3
              ul.lista-ul
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Tcnico:] deben tener una pendiente suave (idealmente menor a 20), superficies antideslizantes (con listones transversales) y barandas laterales slidas para evitar que los animales se distraigan o intenten saltar.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Normativo:] el manual exige que las rampas sean seguras y faciliten un movimiento fluido y sin miedo.
                li
                  i.fas.fa-horse-head
                  p.mb-0 #[b Bienestar:] un ngulo muy pronunciado o un piso resbaladizo genera pnico, resistencia y cadas, convirtiendo la carga en un evento traumtico y peligroso.
