      h5.mb-0 Especificaciones tcnicas: sistemas de vigilancia y monitoreo
    p.mb-4(data-aos="zoom-in") Aunque la normativa no exige sistemas de videovigilancia obligatorios, s establece la necesidad de #[b observacin regular] y protocolos de inspeccin en ruta que requieren capacidad de monitoreo. Por ello, es importante tener en cuenta lo siguiente:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 2.
      i Especificaciones tcnicas del sistema de vigilancia y monitoreo
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle del requisito y protocolo
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Observacin regular]
                td(data-label="Detalle del requisito y protocolo") La disposicin de los animales dentro de la carrocera debe permitir que el conductor o tripulante tenga #[b capacidad de observacin] sobre la carga. Esto es vital para detectar animales cados o signos de estrs.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 4.e:] "Disponer los animales en la unidad de transporte de forma tal que se garantice su #[b observacin regular]".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Inspeccin en Ruta (Uso)]
                td(data-label="Detalle del requisito y protocolo") Se establece un protocolo de #[b inspeccin obligatorio] durante las paradas. El conductor o tripulante debe revisar la unidad y los animales en movimiento (en el parqueadero o rea segura).
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 4.g:] "El conductor o tripulante deber realizar la #[b inspeccin de la unidad de transporte] de animales y de los animales en movimiento, por lo menos cada #[b cuatro (4) horas] durante el viaje".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Monitoreo en guacales/contenedores]
                td(data-label="Detalle del requisito y protocolo") Para especies transportadas en contenedores (guacales), estos deben tener las aberturas necesarias para garantizar el monitoreo del estado del animal durante el transporte.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.c, literal ii:] "Contar con aberturas que permitan la #[b inspeccin] de las aves, los pollitos de un da de edad y de otras especies".
    p.mb-4(data-aos="zoom-in") El control de la temperatura y la ventilacin (el microclima) es uno de los factores ms importantes para reducir el estrs fisiolgico, as como la mortalidad durante el transporte y por este motivo, se deben tener las siguientes indicaciones:
    
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 3.
      i Especificaciones de la temperatura y la ventilacin
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Requisitos de diseo y control
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Proteccin climtica (Diseo)]
                td(data-label="Requisitos de diseo y control") La carrocera debe tener un #[b techo o cubierta] que proteja directamente de las inclemencias. Esto incluye radiacin solar directa, lluvia o granizo, que afectan el bienestar trmico.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.d, literal iii:] "Debe ofrecer proteccin de la #[b radiacin solar directa, lluvia o granizo]".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Ventilacin mnima requerida]
                td(data-label="Requisitos de diseo y control") Todos los vehculos deben tener #[b dispositivos para mantener la ventilacin constante]. Esto se puede lograr mediante aberturas laterales, rejillas o, idealmente, sistemas mecnicos.
                td(data-label="Documento y cita exacta") Manual, Numeral 4.f: "Los vehculos debern contar con dispositivos para mantener la #[b ventilacin constante] ...".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Rango de temperatura (Microclima)]
                td(data-label="Requisitos de diseo y control") El sistema de ventilacin (natural o mecnica) debe ser capaz de asegurar que la temperatura dentro de la unidad de transporte se mantenga en un rango considerado seguro para la mayora de las especies.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 4.f: "]...asegurando un rango de #[b temperatura entre 5 C y 30 C] si no se cuenta con ventilacin mecnica".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Uso de termmetros]
                td(data-label="Requisitos de diseo y control") El transportador debe tener herramientas (como #[b termmetros o dispositivos de medicin]) para verificar y controlar las condiciones de microclima y humedad dentro de la unidad, especialmente en viajes largos o en climas extremos.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 4.f (complemento):] "El transportador deber contar con los dispositivos de medicin para verificar y controlar las condiciones de microclima y humedad dentro de la unidad de transporte".
    p.mb-4(data-aos="zoom-in") La tecnologa permite una gestin activa del bienestar y la logstica durante el viaje, por lo que es importante tener conocimiento de los siguientes procesos tcnicos, normativos y de bienestar:
    
