    separador
    #t_1_3.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.3 Ficha tcnica de homologacin del transporte terrestre
    .row.justify-content-center.mb-4
      .col-lg-9.order-lg-1.order-2.mb-lg-0
        p(data-aos="fade-left") Todo vehculo debe cumplir con lo establecido en su ficha tcnica de homologacin (registro inicial). No se puede modificar la carrocera (dimensiones o capacidad de carga) alterando las condiciones bajo las cuales fue aprobada por el Ministerio de Transporte, aunque s se permiten modificaciones internas para el bienestar animal (divisores, pisos) siempre que no alteren la estructura homologada.
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-1.p-0.px-2.d-none.d-lg-block
            figure
              img.img-fluid(src="@/assets/curso/temas/intro/img4.svg" alt="Icono 1")
          .col-lg-11
            p.mb-0 La ficha tcnica de homologacin (implcita en la licencia de trnsito y el registro RUNT) es el documento legal que establece las caractersticas dimensionales, de peso y de diseo original bajo las cuales el vehculo fue autorizado para circular por el Ministerio de Transporte.
        .tarjeta.color-primarios.p-4
          .row
            .col-lg-1.p-0.d-none.d-lg-block
              figure
                img.img-fluid(src="@/assets/curso/temas/tema1/img26.svg" alt="Icono 1")
            .col-lg-11
              p.mb-0  Es as como el manual de procedimientos utiliza esta ficha tcnica como un lmite inalterable para las modificaciones estructurales de la carrocera, garantizando que el vehculo mantenga su seguridad vial y su capacidad original.
      .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img27.png" alt="Imagen decorativo")
    p(data-aos="fade-left") Igualmente, la ficha tcnica define legalmente la capacidad de carga del vehculo. Esta capacidad no solo es un requisito de seguridad vial (pesos por eje), sino que tambin es un factor limitante para la cantidad mxima de animales a transportar.
    p.mb-4(data-aos="zoom-in") Por lo anterior, es importante tener en cuenta lo siguiente:
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 4.
      i Aspectos presentes en la ficha de homologacin
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle tcnico
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Definicin de capacidad de carga]
                td(data-label="Detalle tcnico") Es el #[b mximo tonelaje autorizado] en un vehculo automotor de carga, cuya tara y peso bruto vehicular no excede los lmites establecidos en las normas vigentes del Ministerio de Transporte.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 1. Definiciones (Capacidad de Carga):] "Es el mximo tonelaje autorizado en un vehculo automotor de carga, cuya tara y peso bruto vehicular, no exceda los lmites establecidos en las normas vigentes del Ministerio de Transporte".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Inalterabilidad]
                td(data-label="Detalle tcnico") La #[b capacidad de carga consignada en la licencia de trnsito no podr alterarse.] Esta cifra es el lmite superior para la carga total, incluyendo el peso de los animales y la estructura de la carrocera.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 1. Definiciones (Capacidad de Carga):] "No podr alterar la Capacidad de Carga consignada en la licencia de trnsito".
    p.mb-4(data-aos="zoom-in")  Si bien la estructura y la capacidad de peso no pueden alterarse, el manual permite modificaciones internas especficas, siempre y cuando estas mejoras estn directamente orientadas a la proteccin y bienestar de los animales, sin afectar la estructura homologada. Adicional a esto, la siguiente tabla detalla las permisiones a tener en cuenta:
    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
