                tr(data-aos="fade-left")
                  td(data-label="Aspecto") #[b Modificaciones internas]
                  td(data-label="Detalle de la permisin") #[b Se permiten modificaciones internas para el bienestar animal] (divisores, pisos) siempre que no alteren la estructura homologada.
                  td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.e (complemento):] "... s se permiten modificaciones internas para el bienestar animal (divisores, pisos) siempre que no alteren la estructura homologada".

                tr(data-aos="fade-left")
                  td(data-label="Aspecto") #[b Propsito de las modificaciones]
                  td(data-label="Detalle de la permisin") Estas modificaciones (ej: instalar pisos antideslizantes ms eficaces, colocar un techo ms resistente a la temperatura, agregar separadores desmontables) son aceptables porque mejoran el #[i confort] y la seguridad de la carga viva sin comprometer la seguridad vial del vehculo.
                  td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.d, literal ii:] "Los pisos de la unidad de transporte deben ser antideslizantes para evitar cadas y fracturas, utilizando cama cmoda y seca, si las condiciones lo americano". (Justifica la mejora del piso).

    separador
    #t_1_4.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.4 Registro RUNT

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-9.order-lg-1.order-2.mb-lg-0
        p(data-aos="fade-left") El Registro nico Nacional de Trnsito (RUNT), si bien es un sistema de informacin y no un documento fsico como tal, es el soporte fundamental que valida legalmente las caractersticas y la aptitud del vehculo para el transporte de carga, incluyendo animales en pie. 
        .tarjeta.color-secundario.p-4.mb-4
          .row.justify-content-center.align-items-center
            .col-lg-12
              p.mb-0 Su relevancia en el marco normativo del bienestar animal reside en la obligatoriedad de concordancia entre los datos registrados y las condiciones fsicas y operativas del vehculo.
        p.mb-0 El RUNT es la plataforma centralizada que administra toda la informacin relativa a vehculos, conductores, licencias y empresas de transporte en Colombia. Al respecto se emiten los siguientes aspectos y normativas:
      .col-lg-3.col-sm-6.col-8.order-lg-2.order-1.mb-lg-0.mb-4(data-aos="zoom-in")
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img28.png" alt="Imagen decorativo")

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 6.
      i Procesos RUNT
    .row.justify-content-center.align-items-center
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle de la funcin
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Definicin operativa]
                td(data-label="Detalle de la funcin") Aunque no est definida en el glosario especfico del manual, la obligatoriedad del RUNT se desprende de su funcin como #[b registro de la licencia de trnsito] y las caractersticas vehiculares aprobadas por el Ministerio de Transporte.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.e:] Implcitamente se refiere al RUNT al hablar de las condiciones bajo las cuales fue aprobado el vehculo por el Ministerio de Transporte.

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Obligatoriedad de registro]
                td(data-label="Detalle de la funcin") Todo vehculo automotor de carga, incluyendo los destinados al transporte de animales en pie, debe estar #[b debidamente matriculado] y con su informacin actualizada en el RUNT.
                td(data-label="Documento y cita exacta") #[b Ley 769 de 2002 (Cdigo Nacional de Trnsito, referencia legal):] establece la obligatoriedad del registro.
    p.mb-4(data-aos="zoom-in") La informacin almacenada en el RUNT es la que define la identidad legal del vehculo y establece los lmites para su uso, especialmente en lo relativo a la capacidad de carga y el tipo de carrocera; destacando lo siguiente:
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 7.
      i Validaciones RUNT
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle de la conexin tcnica
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Validacin de capacidad de carga]
                td(data-label="Detalle de la conexin tcnica") El RUNT contiene la cifra de #[b capacidad de carga] autorizada (mximo tonelaje). Esta cifra es la que el transportador debe respetar y no alterar, siendo un factor crucial en el clculo de la densidad animal (no se puede exceder el peso bruto vehicular autorizado).
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 1. Definiciones (Capacidad de Carga):] "No podr alterar la Capacidad de Carga consignada en la licencia de trnsito". (Esta licencia se administra y actualiza va RUNT).

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Validacin de carrocera]
                td(data-label="Detalle de la conexin tcnica") El tipo de carrocera (ej. tipo jaula, estacas, etc.) y sus dimensiones externas homologadas deben estar registradas en el RUNT. Esto asegura que el vehculo no ha sufrido #[b modificaciones estructurales] no autorizadas que comprometan su seguridad.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.e:] "No se podr modificar la carrocera (dimensiones o capacidad de carga) alterando las condiciones bajo las cuales fue aprobada por el Ministerio de Transporte". (La aprobacin est registrada en el RUNT).
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-9.mb-lg-0.mb-4
        .tarjeta.color-primarios.p-4
          .row.justify-content-center.align-items-center
            .col-lg-2.p-0.d-none.d-lg-block
              figure
                img.img-fluid(src="@/assets/curso/temas/tema1/img30.svg" alt="Icono 1")
            .col-lg-10
              p.mb-0 Las autoridades de trnsito y el #[b Instituto Colombiano Agropecuario (ICA)], utilizan la informacin del RUNT (a travs de la verificacin de la licencia de trnsito) para ejercer vigilancia y control. Basado en esto, se debe tener en cuenta lo siguiente:
      .col-lg-3
        figure
          img.img-fluid(src="@/assets/curso/temas/tema1/img31.png" alt="Icono 1")
    p.mb-4(data-aos="zoom-in") La informacin almacenada en el RUNT es la que define la identidad legal del vehculo y establece los lmites para su uso, especialmente en lo relativo a la capacidad de carga y el tipo de carrocera; destacando lo siguiente:
    
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 8.
      i Vigilancia de autoridades basadas en el RUNT
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle del uso en control
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Exigibilidad por autoridades]
                td(data-label="Detalle del uso en control") Las autoridades de trnsito y el ICA tienen la facultad de #[b exigir la documentacin] que demuestre la legalidad y la concordancia de las especificaciones del vehculo, siendo el RUNT la base de datos para esta verificacin.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 6. Requisitos de las Capacitaciones:] "El cual podr ser exigido por las #[b autoridades de trnsito y/o por el Instituto Colombiano Agropecuario - ICA ]". (Aplica a toda la documentacin legal del vehculo y el conductor).

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Garanta sanitaria]
                td(data-label="Detalle del uso en control") Un vehculo con un RUNT en regla y con una #[b carrocera adecuada] (tipo jaula o similar) que se ajuste a las normas, es un prerrequisito para la expedicin y validez de la #[b Gua Sanitaria de Movilizacin Interna (GSMI)], un documento que s es directamente sanitario y obligatorio.
                td(data-label="Documento y cita exacta") Principio de control sanitario, donde la legalidad del vehculo (RUNT) soporta la legalidad de la movilizacin (GSMI).
    .row.justify-content-center.align-items-center
      .col-lg-10
        p.mb-0 #[b En resumen], el RUNT es el #[b depsito de informacin oficial] que certifica que el vehculo de transporte de animales tiene #[b la matrcula, la capacidad de carga y las caractersticas de carrocera] que lo hacen legal y seguro para operar, siendo un requisito indirecto pero fundamental para el cumplimiento de la normativa de transporte terrestre.
    separador
    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.5 Tipos de documentos legales
    p(data-aos="fade-left") La documentacin legal requerida se clasifica en tres grandes grupos, cubriendo la legalidad de la carga (animales), del vehculo y del personal encargado de la operacin.
    p.mb-4(data-aos="zoom-in") Se invita a que analice la siguiente imagen explicativa sobre esta documentacin:
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .titulo-sexto.color-acento-contenido(data-aos="fade-left")
          h5 Figura 2.
          i  Tipos de documentos legales
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10.movil(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema1/img32_1.png', alt='Figura 2 que contiene diferentes tipos de documentacin y registros del ICA que se deben tener en cuenta al momento de realizar el transporte terrestre del animal en pie.')
      .col-lg-10.desktop(data-aos="fade-left")
        figure
          img(src='@/assets/curso/temas/tema1/img32.png', alt='Figura 2 que contiene diferentes tipos de documentacin y registros del ICA que se deben tener en cuenta al momento de realizar el transporte terrestre del animal en pie.')

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Documentacin sanitaria obligatoria (la carga)
    p.mb-4(data-aos="zoom-in") El documento sanitario es el ms crtico para la movilizacin, ya que certifica que los animales cumplen con los requisitos de sanidad animal exigidos por el pas y debe aplicar lo siguiente:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 9.
      i Documentacin sanitaria obligatoria
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Documento
                th Detalle de la funcin y uso
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Documento") #[b Gua Sanitaria de Movilizacin Interna (GSMI)]
                td(data-label="Detalle de la funcin y uso") Es el documento legal expedido por el #[b Instituto Colombiano Agropecuario (ICA)] que ampara la movilizacin de los animales dentro del territorio nacional. Certifica el origen, destino y propsito de la movilizacin, siendo una herramienta fundamental para la #[b trazabilidad sanitaria y fitosanitaria]. Su porte es obligatorio.
                td(data-label="Documento y cita exacta") El manual hace referencia a la movilizacin de animales, que est intrnsecamente ligada a la GSMI, documento regido por el #[b ICA]. El incumplimiento puede acarrear medidas sanitarias o de bienestar.

              tr(data-aos="fade-left")
                td(data-label="Documento") #[b Certificado de inspeccin sanitaria]
                td(data-label="Detalle de la funcin y uso") En caso de que se requiera la inspeccin previa de los animales antes del transporte (ej. para identificar animales no aptos para el transporte), los documentos de soporte de esa inspeccin deben estar disponibles.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 5. De los Planes de Contingencia y Emergencia (Implcito):] establece la obligacin de atencin y reporte ante animales enfermos o lesionados, lo cual requiere seguimiento documental.


    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Documentacin legal del vehculo (la unidad de transporte)
    p.mb-4(data-aos="zoom-in") Estos documentos certifican la legalidad del vehculo para operar en las vas nacionales como automotor de carga y validan sus especificaciones tcnicas. Estos son dichos documentos:

    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
        h5 Tabla 10.
        i Documentacin legal del vehculo
      .row.justify-content-center.align-items-center.mb-4
        .col-lg-10
          .tabla-a.color-acento-contenido.table-wrapper
            table.tabla-responsive
              thead
                tr(data-aos="fade-left")
                  th Documento
                  th Detalle de la funcin y uso
                  th Documento y cita exacta
              tbody
                tr(data-aos="fade-left")
                  td(data-label="Documento") #[b Licencia de trnsito (tarjeta de propiedad)]
                  td(data-label="Detalle de la funcin y uso") Documento pblico que identifica legalmente un vehculo y da fe de sus caractersticas tcnicas registradas, incluyendo la #[b capacidad de carga]. Este registro est consignado en el RUNT.
                  td(data-label="Documento y cita exacta") #[b Manual, Numeral 1. Definiciones (Capacidad de Carga):] "No podr alterar la Capacidad de Carga consignada en la #[b licencia de trnsito]".

                tr(data-aos="fade-left")
                  td(data-label="Documento") #[b Ficha tcnica de homologacin]
                  td(data-label="Detalle de la funcin y uso") Documento base que define el diseo y las dimensiones originales del vehculo, siendo la referencia para verificar que #[b no se ha modificado la carrocera] o capacidad de carga, garantizando la seguridad vial y estructural.
                  td(data-label="Documento y cita exacta") Manual, Numeral 2.2.e: "No se podr modificar la carrocera (dimensiones o capacidad de carga) alterando las condiciones bajo las cuales fue aprobada por el Ministerio de Transporte...".

                tr(data-aos="fade-left")
                  td(data-label="Documento") #[b Seguro obligatorio (SOAT) y revisin tcnico-mecnica]
                  td(data-label="Detalle de la funcin y uso") Documentos bsicos de trnsito que deben estar vigentes y que son exigibles por la autoridad competente (Polica de Trnsito y Transporte) para todo vehculo automotor de carga.
                  td(data-label="Documento y cita exacta") #[b Ley 769 de 2002 (Cdigo Nacional de Trnsito, referencia legal externa al manual, pero obligatorio).]

            
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Documentacin de habilitacin del personal (conductor/tripulante)
    
    p.mb-4(data-aos="zoom-in") La tripulacin debe demostrar conocimiento y capacitacin en el manejo humanitario de los animales. Al respecto, se debe tener en cuenta:

    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 11.
      i Documentacin de habilitacin del personal
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Documento
                th Detalle de la funcin y uso
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Documento") #[b Certificado de curso de capacitacin en bienestar animal]
                td(data-label="Detalle de la funcin y uso") Documento que acredita que el personal (conductor/tripulante) ha realizado y aprobado el curso de capacitacin exigido. Es obligatorio #[b portar este documento] durante el transporte.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 6. Requisitos de las Capacitaciones:] "Toda persona involucrada en el proceso de transporte de animales en pie deber realizar un curso de capacitacin en bienestar animal y #[b portar el documento que certifique o conste que aprob el curso ...]".

              tr(data-aos="fade-left")
                td(data-label="Documento") #[b Exigibilidad del certificado]
                td(data-label="Detalle de la funcin y uso") Las autoridades encargadas de la vigilancia y control tienen la facultad legal de solicitar este certificado en cualquier momento durante el viaje.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 6:] "...y el cual podr ser exigido por las #[b autoridades de trnsito y/o por el Instituto Colombiano Agropecuario - ICA ]".

              tr(data-aos="fade-left")
                td(data-label="Documento") #[b Licencia de conduccin]
                td(data-label="Detalle de la funcin y uso") Documento personal que habilita al conductor para manejar vehculos de transporte de carga (categora acorde al vehculo).
                td(data-label="Documento y cita exacta") #[b Ley 769 de 2002 (Cdigo Nacional de Trnsito, referencia legal externa).]
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Documentacin operativa (recomendada)
    
    p.mb-4(data-aos="zoom-in") Aunque no son documentos de carcter legal sancionatorio, su porte es parte de las buenas prcticas de manejo y procedimiento exigidas por el manual y se basan con lo siguiente:

    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
        h5 Tabla 12.
        i Documentacin operativa
      .row.justify-content-center.align-items-center.mb-4
        .col-lg-10
          .tabla-a.color-acento-contenido.table-wrapper
            table.tabla-responsive
              thead
                tr(data-aos="fade-left")
                  th Documento
                  th Detalle de la Funcin y Uso
                  th Documento y cita exacta
              tbody
                tr(data-aos="fade-left")
                  td(data-label="Documento") #[b Plan de ruta y bitcora de viaje]
                  td(data-label="Detalle de la funcin y uso") Documento interno que registra la #[b planeacin del viaje] (tiempos estimados, distancias) y la bitcora es donde se registran las paradas, los tiempos de descanso (cada 8 horas de viaje), las inspecciones realizadas a la carga y cualquier evento o emergencia presentada.
                  td(data-label="Documento y cita exacta") #[b Manual, Numeral 4. Requisitos del Proceso...:] establece la obligatoriedad de los tiempos de descanso y las inspecciones peridicas (cada 4 horas), lo que requiere un registro operativo.

                tr(data-aos="fade-left")
                  td(data-label="Documento") #[b Plan de contingencia (protocolos)]
                  td(data-label="Detalle de la funcin y uso") Protocolo escrito para atender situaciones de emergencia (animales cados, traumatismos, accidentes). El conductor debe conocer las #[b instrucciones para atender la situacin], aunque el documento fsico pueda ser ms de referencia en la empresa.
                  td(data-label="Documento y cita exacta") #[b Manual, Numeral 5. De los Planes de Contingencia y Emergencia:] obliga a tener en cuenta las instrucciones del manual para atender emergencias.

    separador
    #t_1_6.titulo-segundo.color-acento-contenido(data-aos="fade-left")
      h2 1.6 Condiciones sanitarias de estructura de carrocera
    p.mb-4(data-aos="zoom-in") Las condiciones sanitarias de la carrocera son fundamentales para prevenir la propagacin de enfermedades infectocontagiosas entre animales (bioseguridad) y asegurar la inocuidad del producto final, al evitar que los fluidos corporales contaminen el vehculo, el entorno y los animales transportados.
    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Requisitos sanitarios de diseo y materiales
    p.mb-4(data-aos="zoom-in") La eleccin de materiales y el diseo de la estructura deben facilitar la limpieza, que es el paso ms importante de la desinfeccin. Para dar continuidad a este proceso, es necesario tener en cuenta los siguientes detalles y normativa:
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 13.
      i Estructura y funcin sanitaria
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle de la estructura y funcin sanitaria
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Paredes y superficies lisas]
                td(data-label="Detalle de la estructura y funcin sanitaria") Las paredes internas deben ser lisas y sin salientes no solo para evitar lesiones, sino para facilitar su limpieza y desinfeccin. Las superficies irregulares o rugosas atrapan materia orgnica (heces, sangre) que impiden la accin de los desinfectantes.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.d, literal i:] "Sern lisas, seguras, sin salientes, ni bordes puntiagudos que puedan causar lesiones y deben facilitar su limpieza y desinfeccin".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Esquinas y juntas]
                td(data-label="Detalle de la estructura y funcin sanitaria") Se priorizan diseos con bordes redondeados en lugar de ngulos rectos (difciles de limpiar). Esto reduce los puntos donde se pueden acumular residuos orgnicos y patgenos.
                td(data-label="Documento y cita exacta") #[b Manual, Numeral 2.2.c, literal i:] (Sobre los contenedores plsticos, porcinos y aves) "deben ser lisas, con bordes redondeados, que permitan su lavado y desinfeccin". (Este principio se extiende a toda la carrocera).

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Materiales no absorbentes]
                td(data-label="Detalle de la estructura y funcin sanitaria") La estructura debe ser de materiales impermeables (metal tratado, fibra de vidrio o plsticos sanitarios) que no absorban fluidos, lo que reduce la contaminacin cruzada y el riesgo sanitario.
                td(data-label="Documento y cita exacta") (Implcito en el requisito de ser "lisas" y "facilitar la limpieza". Los materiales higinicos son un estndar en la industria pecuaria).

    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Gestin sanitaria de los pisos y drenaje
    p.mb-4(data-aos="zoom-in") El manejo de las excretas es el requisito sanitario ms especfico del diseo de la carrocera, siendo las heces y la orina el principal foco de riesgo biolgico.
    .BMG1.px-md-5.px-4
      .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
        h5 Tabla 14.
        i Gestin sanitaria
      .row.justify-content-center.align-items-center.mb-4
        .col-lg-10
          .tabla-a.color-acento-contenido.table-wrapper
            table.tabla-responsive
              thead
                tr(data-aos="fade-left")
                  th Aspecto
                  th Detalle de la gestin sanitaria
                  th Documento y cita exacta
              tbody
                tr(data-aos="fade-left")
                  td(data-label="Aspecto") #[b Prevencin de derrames]
                  td(data-label="Detalle de la gestin sanitaria") El diseo del piso es crtico para minimizar la filtracin o derrame de orina y heces hacia el exterior o sobre los animales en niveles inferiores (en camiones de doble piso). Esto es un requisito de sanidad ambiental y bioseguridad.
                  td(data-label="Documento y cita exacta") Manual, Numeral 2.2.d, literal ii: "Deben minimizar la filtracin o derrame de orina y heces hacia el exterior, o niveles inferiores si el transporte es de doble piso".

                tr(data-aos="fade-left")
                  td(data-label="Aspecto") #[b Uso de cama sanitaria]
                  td(data-label="Detalle de la gestin sanitaria") El uso de cama (aserrn, viruta o tamo) sobre el piso, cuando las condiciones lo ameritan, es una medida sanitaria y de bienestar que ayuda a absorber la humedad y fluidos, mejorando la higiene del ambiente interno y la estabilidad del animal.
                  td(data-label="Documento y cita exacta") Manual, Numeral 2.2.d, literal ii: "...utilizando cama confortable y seca, si las condiciones lo ameritan."


    .titulo.mb-4(data-aos="fade-left")
      img(src='@/assets/curso/temas/icon.svg', alt='Imagen decorativa')
      h5.mb-0 Protocolo sanitario y de bioseguridad (uso)
    p.mb-4(data-aos="zoom-in") Las condiciones sanitarias de la estructura no son permanentes; todo depende de la correcta aplicacin de los protocolos de limpieza y desinfeccin antes y despus de cada viaje. Por este motivo es importante tener presente los siguientes detalles:
    .titulo-sexto.color-acento-contenido.offset-0.offset-lg-1(data-aos="fade-left")
      h5 Tabla 15.
      i Protocolo sanitario
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tabla-a.color-acento-contenido.table-wrapper
          table.tabla-responsive
            thead
              tr(data-aos="fade-left")
                th Aspecto
                th Detalle del protocolo sanitario
                th Documento y cita exacta
            tbody
              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Limpieza y desinfeccin obligatoria]
                td(data-label="Detalle del protocolo sanitario") La carrocera debe ser sometida a un riguroso proceso de #[b limpieza y desinfeccin antes y despus de cada uso]. Esto es esencial para cortar el ciclo de transmisin de enfermedades entre lotes de animales.
                td(data-label="Documento y cita exacta") Manual, Numeral 2.2.a: "La unidad de transporte deber ser lavada y desinfectada antes y despus de cada uso".

              tr(data-aos="fade-left")
                td(data-label="Aspecto") #[b Orden del proceso]
