        p.mb-0 La magnitud de carga en el transporte de animales en pie es un concepto dual que abarca tanto el lmite de peso legal del vehculo (capacidad de carga), como la distribucin de espacio por animal (densidad de carga), siendo esta ltima la medida fundamental del bienestar animal.
